@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'lato', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
div.form-wrap {
    width: 30%;
    height: 5rem;
    margin: 0 auto;
    background: rgba(255, 255, 255, 0.3);
    text-align: center;
    border-radius: 10px;
    box-shadow: 1px 1px 1px 0 rgba(255, 255, 255, 0.3);
    display: flex;
    justify-items: center;
    line-height: 5rem;
    /* border: 1px solid black; */
    margin-top: 7rem;
}

div.form-wrap form {
    width: 100%;
}

div.form-wrap input {
    width: 50%;
    height: 100%;
    border: 0;
    background-color: transparent;
    padding: 0 10px;
    outline: 0;
    /* border-radius: 20px; */
    /* border: 1px solid red; */
}

div.form-wrap button {
    width: 40%;
    height: 50%;
    border: 0;
    margin: 0 1rem;
    cursor: pointer;
    background: #113275;
    /* border: 1px solid green; */
    color: white;
    font-size: 1.2rem;
    font-weight: lighter;
    position: relative;
}

div.form-wrap button::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 0.2rem;
    background-color: white;
    transition: 0.2s;
}

div.form-wrap button:hover::after {
    width: 100%;
}

@media (max-width: 960px) {
    div.form-wrap {
        width: 70%;
    }
}
div.result {
    margin-top: 4rem;
}

div.result span {
    display: block;
    font-size: 1.9rem;
    text-align: center;
    margin: 1rem;
    color: white;
}

div.pic {
    display: flex;
    align-items: center;
}

div.pic img {
    width: 10rem;
    margin: 0 auto;
    display: block;
}
